import React, { useEffect, useState } from 'react'
import { useHistory, Link } from 'react-router-dom'
import { Image, Table, Row, Col, Typography, Button, Space, Popconfirm } from 'antd'
import * as anticons from '@ant-design/icons'
import { toast } from 'react-hot-toast'
import Loader from './../components/Loader'

import { getPerfiles, getUserName } from '../utilities'
import UsersService from '../service/UsersService'
const usersService = new UsersService()

const columnas = [
    {
        title: '',
        dataIndex: 'activo',
        render: (_, { activo }) =>
            activo === '1' ? <i className="fa-solid fa-user-check" style={{ color: 'green', fontSize: '1.5em' }}></i> : <i className="fa-solid fa-user-xmark" style={{ color: 'red', fontSize: '1.5em' }}></i>
    },
    {
        title: 'Nombres Apellidos',
        dataIndex: 'nombres',
        render: (_, { id, nombres, apellidoPaterno, apellidoMaterno }) =>
            <Link to={`user/${id}/edit`}>{nombres} {apellidoPaterno} {apellidoMaterno}</Link>,
    },
    {
        title: 'Perfil',
        dataIndex: 'idPerfil',
        render: (_, { idPerfil }) => getPerfiles().filter(p => p.id === idPerfil)[0].nombres || 'No definido'
    },
    {
        title: 'Correo',
        dataIndex: 'correo',
        responsive: ['md', 'lg', 'xl', 'xxl']
    },
    {
        title: 'Supervisor Responsable',
        dataIndex: 'nombreResponsable',
        responsive: ['lg', 'xl', 'xxl']
    },
    {
        title: 'Telfono',
        dataIndex: 'telefono',
        responsive: ['lg', 'xl', 'xxl']
    },
    {
        title: '',
        dataIndex: 'password',
        render: (_, { correo }) =>
            <div style={{
                textAlign: 'center'
            }}>
                <Popconfirm
                    title="Restablecer contrasea"
                    icon={<anticons.QuestionCircleFilled style={{ color: '#336699' }} />}
                    description="Desea enviar un correo al usuario para restablecer su contrasea?"
                    onConfirm={async () => {
                        const user = {
                            correo,
                            responsable: getUserName()
                        }
                        try {
                            const { data } = await usersService.sendResetPassword({ user })
                            const { detalle, estado } = data
                            if (estado)
                                toast.success(detalle)
                            else
                                toast.error(detalle)
                        } catch ({ response }) {
                            const { data } = response
                            toast.error(data.detalle);
                        }
                    }}
                    okText="Si"
                    cancelText="No"
                >
                    <i title='Enviar correo para restablecer contrasea' className="fa-solid fa-paper-plane" style={{ color: '#336699', fontSize: '1.5em' }}></i>
                </Popconfirm>
            </div>,
    },
]

const Users = () => {

    const navigate = useHistory()
    const [cargar, setCargar] = useState(true);
    const [cargando, setCargando] = useState(false);
    const [visualizar, setVisualizar] = useState(false);
    const [cargarUsers, setCargarUsers] = useState(false)
    const [procesando, setProcesando] = useState(false)
    const [users, setUsers] = useState([])

    const go = () => navigate.push('/usernew/new')

    const gets = async () => {
        setProcesando(true)
        try {
            const { data } = await usersService.gets({ navigate, responsable: getUserName() })
            setUsers(data)
        } catch ({ response }) {
            const { data } = response
            toast.error(data.detalle)
        } finally {
            setCargarUsers(false)
            setProcesando(false)
        }
    }

    const validar = async () => {
        setCargando(true);
        try {
            const { data } = await usersService.isAuthenticated({
                navigate,
                responsable: getUserName(),
            });
            const { estado, detalle } = data;
            if (estado === false) {
                setTimeout(() => {
                    toast(detalle);
                    navigate.push("/login");
                }, 2000);
            } else {
                setCargarUsers(true);
                setVisualizar(true);
            }
        } catch ({ response }) {
            const { data } = response;
            const { estado, detalle } = data;
            if (estado === false) {
                setTimeout(() => {
                    toast.error(detalle);
                    navigate.push("/login");
                }, 2000);
            } else {
                setCargarUsers(true);
                setVisualizar(true);
            }
        } finally {
            setCargar(false);
            setCargando(false);
        }
    };

    useEffect(() => {
        if (cargar && !cargando) validar();
    });

    useEffect(() => {
        if (cargarUsers && !procesando) {
            gets()
        }
    })

    if (cargar === false && cargando === false && visualizar === true) {
        return (
            <>
                <div
                    style={{
                        color: "#333333",
                        padding: "20px 0",
                    }}
                >
                    <Image
                        alt="BYDZYNE"
                        src="/assets/images/logoBlack.png"
                        style={{ height: "80px", width: "80px" }}
                        preview={false}
                    />
                    <span
                        style={{
                            marginLeft: "20px",
                            fontSize: "1.5em",
                            letterSpacing: "3px",
                            fontWeight: "bold",
                        }}
                    >
                        BYDZYNE
                    </span>
                </div>
                <Row>
                    <Col>
                        <Typography.Title level={2}>
                            Usuarios
                        </Typography.Title>
                    </Col>
                </Row>
                <Row justify='end' style={{ paddingBottom: '15px' }}>
                    <Col>
                        <Space>
                            <Button onClick={() => go()} type='default' disabled={procesando} icon={<anticons.FileAddFilled />}>Nuevo</Button>
                            <Button onClick={() => gets()} type='primary' disabled={procesando} loading={procesando}
                                icon={<anticons.SearchOutlined />}
                            >Consultar</Button>
                        </Space>
                    </Col>
                </Row>
                <Table rowKey='id' columns={columnas} dataSource={users} loading={procesando} />
            </>
        )
    }

    return (
        <>
            <Loader />
        </>
    )
}

export default Users